html {
  font-family: source-han-sans-simplified-c, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 62.5%;
}

.App {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  overflow: hidden;
}

.Fit-width-img {
  object-fit: cover;
  width: 100%;
}

.Cloud-float1 {
  object-fit: cover;
  width: 100%;
  animation: Cloud-float1 1.5s linear 0s infinite alternate;
}

@keyframes Cloud-float1 {
  from {
    top: 20vw;
  }

  to {
    top: 23vw;
  }
}

.Cloud-float2 {
  transform: rotate(5deg);
  animation: Cloud-float2 1.3s linear 0s infinite alternate;
  animation-delay: 333ms;
}

@keyframes Cloud-float2 {
  from {
    top: 60vw;
  }

  to {
    top: 57vw;
  }
}

.Cloud-float3 {
  transform: scaleY(-1) rotate(30deg);
  animation: Cloud-float3 1.3s linear 0s infinite alternate;
}

@keyframes Cloud-float3 {
  from {
    top: 0vw;
  }

  to {
    top: 3vw;
  }
}

.Cloud-float4 {
  transform: rotate(-10deg);
  animation: Cloud-float4 1.2s linear 0s infinite alternate;
  animation-delay: 103ms;
}

@keyframes Cloud-float4 {
  from {
    top: 63vw;
  }

  to {
    top: 60vw;
  }
}

.Plane-fly {
  transform: rotate(-10deg);
  animation: Plane-fly 3s linear infinite;
}

@keyframes Plane-fly {
  from {
    left: -100%;
    top: 65vw;
  }

  to {
    left: 100%;
    top: 5vw;
  }
}

